<template>
  <div>
    <div v-if="!loading" style="height: 500px; width: 100%;">
      <LMap
        :zoom="zoom"
        :center="center"
        style="z-index: 0;"
      >
        <LTileLayer :url="url"></LTileLayer>
        <LMarker
          v-for="(prjct, i) in marks"
          v-on:dblclick="viewProject(prjct.id)"
          :key="i"
          :lat-lng="[prjct.city.latitude, prjct.city.longitude]"
        >
          <l-popup>{{ prjct.title }}</l-popup>
        </LMarker>
      </LMap>
    </div>
    <LoadingComponent v-else :loading="loading" />
    <div class="text-center">
      <v-dialog v-model="dialog" width="500">
        <v-card>
          <v-card-title class="headline grey lighten-2">
            جزئیات پروژه
          </v-card-title>

          <v-card-text class="py-4">
            نام پروژه: {{ showingProject.title }}<br />
            شناسه: {{ showingProject.id }}<br />
            خلاصه: {{ showingProject.summary_description }}<br />
            شرح: {{ showingProject.description }}<br />
            <span>{{ showingProject.is_show === '1' ? 'قابل مشاهده': 'غیر قابل مشاهده'}}</span><br />
          </v-card-text>
          
          <hr class="style-one">
          
          <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn
                  text
                  color="success"
                  :to="'/dashboard/pages/projects/edit-project/' + showingProject.id"
              >
                ویرایش
                <v-icon>mdi-pencil</v-icon>
              </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </div>
  </div>
</template>

<script>
import { LMap, LTileLayer, LMarker, LPopup } from "vue2-leaflet";
import authHeader from "../../middleware/authHeader";

export default {
  name: "MapProjectsVisual",
  props: {
    labelRange: {
      type: Array,
    }
  },
  components: {
    LoadingComponent: () => import("@/components/commonComponents/LoadingComponent"),
    LMap,
    LTileLayer,
    LMarker,
    LPopup,
  },
  data() {
    return {
      url: "https://{s}.tile.osm.org/{z}/{x}/{y}.png",
      zoom: 6,
      center: [35.6892, 51.3890],
      bounds: null,
      marks: [],
      showingProject: "",
      dialog: false,
      loading: false,
    };
  },
  methods: {
    fetchData: async function () {
      this.loading = true;
      this.marks = [];
      const dateTime = {
        first_date: new Date().toISOString().slice(0,-5),
        to_date: new Date().toISOString().slice(0,-5)
      }
      if (this.labelRange) {
        dateTime.first_date = new Date(new Date().setMonth(new Date().getMonth() - this.labelRange[1])).toISOString().slice(0, -5);
        dateTime.to_date = new Date(new Date().setMonth(new Date().getMonth() - this.labelRange[0])).toISOString().slice(0,-5);
      }
      let queryOptions = "";
      queryOptions = queryOptions + ` from_date: "${dateTime.first_date}" \n`;
      queryOptions = queryOptions + ` to_date: "${dateTime.to_date}" \n`;

      const result = await this.$axios.post(
        "/graphql",
        {
          query: `query {
            reportOrder(query: {
              ${queryOptions}
            }) {
              id
              date
              amount
              label
              city {
                id
                title
                slug
                latitude
                longitude
              }
              province {
                id
                title
                slug
                latitude
                longitude
              }
            }
          }`,
        },
        authHeader()
      );
      if (result.data.errors) {
        console.log(result.data.errors[0]);
      } else if (result.data.data.reportOrder) {
        const projectsRes = result.data.data.reportOrder;
        projectsRes.forEach((pr) => {
          this.marks.push(pr)
        });
      }
      this.loading = false;
    },
    viewProject(id) {
      this.showingProject = this.marks.filter((item) => item.id === id)[0];
      this.dialog = true;
    },
  },
  mounted () {
    this.fetchData();
  },
  watch: {
    labelRange: function () {
        this.fetchData()
    }
  }
};
</script>

<style scoped>
.example-custom-control {
  background: #fff;
  padding: 0.5em 0.5em 0.1em;
  border: 1px solid #aaa;
  border-radius: 0.1em;
}
.custom-control-watermark {
  font-size: 200%;
  font-weight: bolder;
  color: #aaa;
  text-shadow: #555;
}
</style>